import React, { useLayoutEffect, useRef } from "react";
import Roll from "digit-roll-react";
import clsx from "clsx";

import { scrollAdder } from "./addScrollElement";
import "./style.scss";

const DigitRoll = React.memo(Roll);
const createNum = (num) => {
  const value = num;
  const zeroLength = 4 - String(num).length;
  if (zeroLength > 0) {
    for (let i = 0; i < zeroLength; i++) {
      value = "0" + value;
    }
  }
  return String(value);
};
export const Roller = ({ num, isFail, isAuto }) => {
  const rollerBox = useRef(null);

  useLayoutEffect(() => {
    if (rollerBox.current) {
      scrollAdder(rollerBox.current);
    }
  }, [rollerBox, num]);
  return (
    <div className="Roller__Box__Bet" ref={rollerBox}>
      <DigitRoll
        num={createNum(num)}
        length={4}
        divider=""
        delay="0"
        height={3.75}
        width={3.15}
        className={clsx("rollerClass", {
          ["betTextFail"]: isFail,
          ["betAutoOn"]: isAuto,
        })}
      />
    </div>
  );
};

export default React.memo(Roller);
