import React, { FC, useEffect, useLayoutEffect, useRef, useState } from "react";

import { useWhiteTheme } from "providers";
import { IDiceResult } from "../types.js";
import { Roller } from "./components/Roller/Roller.jsx";

import css from "./style.module.scss";

export interface IDiceComponentProps extends IDiceResult {
  win: 1 | 0;

  onLoadCallback: () => void;
}
export const DiceComponent: FC<IDiceComponentProps> = ({
  number,
  win,
  onLoadCallback,
}) => {
  const [load, setLoad] = useState(false);
  const { isWhiteThemeActive } = useWhiteTheme();
  const includeRef = useRef<HTMLDivElement | null>(null);
  useLayoutEffect(() => {
    if (!load) {
      setLoad(true);
    }
    if (load) {
      setTimeout(() => {
        onLoadCallback();
      }, 500);
    }
  }, [onLoadCallback, load]);

  return (
    <div className={css.root}>
      <div
        className={css.dataWrapper}
        ref={(node) => {
          includeRef.current = node;
        }}>
        <img
          loading="lazy"
          src={
            !isWhiteThemeActive
              ? "/assets/dice/img/darkPanel.png"
              : "/assets/dice/img/whitePanel.png"
          }
          alt="dice panel"
          className={css.imgPanel}
        />
        <Roller isAuto={false} isFail={win === 0} num={"0105"} />
      </div>
    </div>
  );
};
