import { track } from "utils";

const fixNumber = (number) => {
  let numResult = number;

  if (numResult > 9) {
    numResult = 0;
  } else if (numResult < 0) {
    numResult = 9;
  }

  return numResult;
};

const addElement = (element, direction, number) => {
  const className =
    direction === "before"
      ? "Roller__beforeNumber"
      : direction === "after"
      ? "Roller__afterNumber"
      : null;

  const place =
    direction === "before"
      ? "beforeend"
      : direction === "after"
      ? "afterbegin"
      : null;
  element.insertAdjacentHTML(
    place,
    `
      <div class="${className}_bet">${number}</div>
      `,
  );
};

export const scrollAdder = (element) => {
  try {
    const collection = [...element.firstChild.firstChild.children];
    if (!collection) return null;

    collection.forEach((value) => {
      if (!value) {
        return;
      }
      if (value.children && value.children.length > 0) {
        const actualElement = value.lastChild;
        const elseElements = [...value.children];
        elseElements.pop();

        elseElements.forEach((value) => {
          value.style.top = "1.5rem";
        });
        actualElement.style.top = "";

        const actualNumber = Number(actualElement.innerHTML);

        if (actualNumber || actualNumber === 0) {
          const prevNumber = fixNumber(actualNumber - 1),
            nextNumber = fixNumber(actualNumber + 1);
          if (value.children.length === 1) {
            addElement(actualElement, "before", prevNumber);
          }

          addElement(actualElement, "after", nextNumber);
        }
      }
    });
  } catch (error) {
    const message = `
${error.stack}
Dice > Panel > Roller > scrollAdder
MessageEvent: ${JSON.stringify(element?.firstChild?.firstChild?.children)}`;
    try {
      track()?.errors(message);
    } catch {
      console.log("🚀 trackError", error);
    }
  }
};
